.nav-two {
  padding: 10px 0 0;
  ul.list-links{
    margin: 6px 0 0;
  }
  a:not(.btn){
    color: $gray-dark;
    display: block;
    font-size: 14px
  }
  .logo {
    margin-top: 15px;
  }
}
.view-search-bar {
  input {
    height: 50px;
    background-color: #f7f7f9;
    padding: 25px;
    &[name="location"] {
      border-radius: 100px 0 0 100px;
    }
    &[name="daterange"] {
      border-radius: 0 100px 100px 0;
    }
  }
}
